import { useRef, useEffect } from 'react';
import { useTranslation } from 'react-i18next';

import { useAppDispatch, useAppSelector } from 'redux/hook';
import {
	BACK_IMG_SRC,
	SEARCH_IMG_SRC,
	NAV_IMG_SRC,
} from 'config/index';
import {
	toggleSearch,
	updateSearchQuery,
	updateMovieList,
} from 'features/GenreMovieList/redux/genreMovieSlice';

import './MovieLayout.scss';

type MovieLayoutProps = {
	children: React.ReactNode;
	heading?: string;
};

export const MovieLayout: React.FC<MovieLayoutProps> = ({
	children,
	heading,
}) => {
	const { isSearchOpen, searchQuery } = useAppSelector(
		(state) => state.genreMovie
	);
	const searchInputRef = useRef<HTMLInputElement>(null);
	const dispatch = useAppDispatch();
	const { t } = useTranslation();

	useEffect(() => {
		if (isSearchOpen && searchInputRef.current) {
			searchInputRef.current.focus();
		}
	}, [isSearchOpen]);

	const handleSearchIconClick = () => {
		dispatch(toggleSearch());
		if (searchQuery) {
			dispatch(updateSearchQuery(''));
			dispatch(updateMovieList(''));
		}
	};

	const handleSearchInputChange = (
		event: React.ChangeEvent<HTMLInputElement>
	) => {
		dispatch(updateSearchQuery(event.target.value));
		dispatch(updateMovieList(event.target.value));
	};

	return (
		<section className="movie-genre-layout">
			<header className="movie-genere-header">
				<nav className="movie-genere-nav">
					<img
						className="genere-nav-img"
						src={NAV_IMG_SRC}
						alt={t('movie.image.navBar')}
					/>
					<div className="genere-nav-header">
						<img
							className="genere-nav-icon"
							src={BACK_IMG_SRC}
							alt={t('movie.image.back')}
						/>
						{isSearchOpen ? (
							<input
								ref={searchInputRef}
								type="text"
								value={searchQuery}
								onChange={handleSearchInputChange}
								placeholder={t('movie.searchPlaceholder')}
							/>
						) : (
							<h1>{heading}</h1>
						)}
					</div>
					{isSearchOpen ? (
						<span
							className="genere-nav-icon"
							onClick={handleSearchIconClick}
						>
							Esc
						</span>
					) : (
						<img
							className="genere-nav-icon"
							src={SEARCH_IMG_SRC}
							alt={t('movie.image.search')}
							onClick={handleSearchIconClick}
						/>
					)}
				</nav>
			</header>
			<main>{children}</main>
		</section>
	);
};
