import { useEffect, useMemo } from 'react';
import InfiniteScroll from 'react-infinite-scroll-component';
import { useTranslation } from 'react-i18next';

import { MovieCard } from 'components/Movie/MovieCard';
import { useAppSelector, useAppDispatch } from 'redux/hook';
import { fetchMovieList } from '../redux/genreMovieSlice';
import { RootState } from 'redux/store';
import PlaceholderImage from 'utils/PreloadPlaceholderImage';
import { Spinner } from 'components/Elements/spinner';
import { MovieLayout } from 'components/Layouts';
import { MOVIE_IMAGE_SRC } from 'config/index';

import '../styles/GenreMovieList.scss';

export const GenreMovieList: React.FC = () => {
	const {
		currentPage,
		movieList,
		loading,
		title,
		error,
		searchQuery,
	} = useAppSelector((state: RootState) => state.genreMovie);

	const dispatch = useAppDispatch();
	const { t } = useTranslation();

	useEffect(() => {
		dispatch(fetchMovieList(1));
	}, [dispatch]);

	const isMovieEmptyAfterSearch = useMemo(
		() => searchQuery && movieList && movieList.length === 0,
		[searchQuery, movieList]
	);

	const handleLoadMore = () => {
		dispatch(fetchMovieList(currentPage + 1));
	};

	if (!movieList) {
		return <Spinner />;
	}

	return (
		<MovieLayout heading={title}>
			{isMovieEmptyAfterSearch && (
				<span>{t('movie.noMovieSearch')}</span>
			)}
			<InfiniteScroll
				dataLength={movieList.length}
				next={handleLoadMore}
				hasMore={!loading && !error}
				loader={
					isMovieEmptyAfterSearch ||
					(error && <span>{t('message.loading')}</span>)
				}
				className="movie-container"
			>
				{movieList.map((movie, index) => (
					<MovieCard
						key={index}
						title={movie.name}
						imageURL={MOVIE_IMAGE_SRC + movie['poster-image']}
						placeholderImage={PlaceholderImage}
					/>
				))}
			</InfiniteScroll>
		</MovieLayout>
	);
};
